import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import MenuIcon from '@material-ui/icons/Menu';



const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
   
  },
  menuButton: {
    marginRight: theme.spacing(2),
    
  },
  title: {
    flexGrow: 1,
   
  },
}));

export default function MenuAppBar({Link}) {
  const classes = useStyles();

  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  
 
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div className={classes.root}>
      
      <AppBar position="static">
        <Toolbar >
          <IconButton edge="start" onClick={handleClick}
          className={classes.menuButton} color="inherit" aria-label="menu">
          <Button  aria-controls="simple-menu" aria-haspopup="true" >
            <MenuIcon/>
      </Button>
            
            
          </IconButton>
          <Typography variant="h6" className={classes.title}>
            El camar√≥n
          </Typography>
          { (
            <div>
              
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={open}
                onClose={handleClose}
              >
                <MenuItem onClick={handleClose}><Link to="/">Home</Link></MenuItem>
            
            <MenuItem onClick={handleClose}><Link to="/registrarse">Registrarse</Link></MenuItem>
            <MenuItem onClick={handleClose}><Link to="/card">Card</Link></MenuItem>
            <MenuItem onClick={handleClose}><Link to="/contacto">Contacto</Link></MenuItem>
              </Menu>
            </div>
          )}
        </Toolbar>
      </AppBar>
    </div>
  );
}
